.to-right {
  display: flex;
  justify-content: space-between;
  img {
    height: 415px;
    width: 450px;
  }
  .to-left {
    text-align: center;
    color: #555;
    font-family: "Roboto";
    font-weight: 300;
    font-size: 32px;
    height: 79vh;
    overflow: hidden;
    margin-left: 1rem;
    margin-top: 1rem;
    width: 161%;
    div {
      overflow: hidden;
      white-space: nowrap;
    }
    div:first-of-type {
      animation: showup 7s infinite;
    }
    div:last-of-type {
      width: 0px;
      animation: reveal 7s infinite;
    }
    div:last-of-type span {
      margin-left: -355px;
      animation: slidein 7s infinite;
    }
  
    @keyframes showup {
      0% {
        opacity: 0;
      }
      20% {
        opacity: 1;
      }
      80% {
        opacity: 1;
      }
      100% {
        opacity: 0;
      }
    }

    @keyframes slidein {
      0% {
        margin-left: -800px;
      }
      20% {
        margin-left: -800px;
      }
      35% {
        margin-left: 0px;
      }
      100% {
        margin-left: 0px;
      }
    }

    @keyframes reveal {
      0% {
        opacity: 0;
        width: 0px;
      }
      20% {
        opacity: 1;
        width: 0px;
      }
      30% {
        width: 355px;
      }
      80% {
        opacity: 1;
      }
      100% {
        opacity: 0;
        width: 355px;
      }
    }
    img{
      width: 200px;
      height: 200px;
      margin-top: 1.5rem;
    }
  }
  .profile {
    display: flex;
    flex-direction: column;
    justify-content: center;
    .heading {
      color: #555;
      font-family: "Roboto";
      font-weight: 300;
      font-size: 25px;
    }
  }
}
